<template>
  <div class="box" :class="animateClass">
    <Head :title="detail.name" :show="true"></Head>
    <div>
      <div class="odiv">
        <p class="op" @click="showPopup" v-for="(s,i) in detail.services" :key="i">{{s.name}}</p>
      </div>
      <div>
        <p style="display:inline-block;font-size:27px;margin:15px 30px;">{{detail.address}}</p>
        <van-icon @click="call(detail.phone)" color="#1989fa" name="phone-o" size="38" />
      </div>
    </div>

    <van-popup v-model="show" position="top">
      <div v-for="(s,i) in detail.services" :key="i">
        <span class="op">{{s.name}}</span>
        <p style="margin:10px 0;padding:3px 35px 10px">{{s.description}}</p>
      </div>
    </van-popup>

    <div class="swipe">
      <Carousel id="b3" :swiperoptions="options">
        <Carousel-item v-for="(film,i) in films" :key="i">
          <img :src="film.poster" alt />
        </Carousel-item>
      </Carousel>
    </div>

    <div
      v-show="active == i"
      @click="gotoFilm(film)"
      class="item"
      v-for="(film,i) in films"
      :key="i"
    >
      <p>
        {{film.name}} /
        <span>{{film.grade}}</span>
      </p>
      <p>{{film.category}} | {{film.runtime + '分钟'}} | {{film.director}}</p>
    </div>
  </div>
</template>


<script>
import { animate } from "@/utils/index.js";
export default {
  mixins: [animate],
  data() {
    return {
      detail: {},
      show: false,
      films: [],
      options: {},
      active: 0
    };
  },
  methods: {
    call(phone) {
      alert(phone);
    },
    showPopup() {
      this.show = !this.show;
    },
    gotoFilm(film) {
      this.$router.push({
        name: "films",
        params: {
          filmId: film.filmId
        },
        query: {
          name: film.name
        }
      });
    }
  },
  mounted() {
    // console.log(this.$route);
    this.$axios
      .get("/maizuo/gateway/?k=417263", {
        //k 类似于时间戳，可有可无
        params: {
          cinemaId: this.$route.params.cinemaId
        },
        headers: {
          "X-Host": "mall.film-ticket.cinema.info"
        }
      })
      .then(res => {
        this.detail = res.data.data.cinema;
      });
  },
  created() {
    this.$axios
      .get("/maizuo/gateway?k=9682114", {
        params: {
          cinemaId: this.$route.params.cinemaId
          // cinemaId: 4876,
        },
        headers: {
          "X-Host": "mall.film-ticket.film.cinema-show-film"
        }
      })
      .then(res => {
        this.films = res.data.data.films;
        var that = this;
        this.options = {
          effect: "coverflow",
          slidesPerView: 3,
          centeredSlides: true,
          coverflowEffect: {
            rotate: 30,
            stretch: 20,
            depth: 10,
            modifier: 2,
            slideShadows: true
          },
          on: {
            slideChange: function() {
              // 监听swiper 切换  千万不能使用箭头函数
              console.log("改变了，activeIndex为" + this.activeIndex);
              // swiper组件中的  this == Swiper 实例
              that.active = this.activeIndex;
            }
          }
        };
      });
  }
};
</script>

<style lang="scss" scoped>
.odiv {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  display: flex;
}
.op {
  border: 1px solid #ffb232;
  // transform: scale(.5);
  border-radius: 1px;
  padding: 3px;
  margin: 6px;
  // font-size: ;
  color: #ff5f16;
}
.item {
  background: deeppink;
  color: #fff;
  font-size: 20px;
}
</style>